import styled from "styled-components";

import {
	colors, displayFontSizes, Margins, spacingMap,
} from "../styleguide";

export interface StyledCheckboxProps {
	isInvalid?: boolean,
	isIndeterminate?: boolean,
}

export interface LabelProps {
  disabled?: boolean,
}

export const CheckboxContainer = styled.div<Margins>`
	display: inline-block;
	position: relative;
	height: 20px;
	line-height: 20px;
	font-size: ${displayFontSizes.headlineSmall.$fontSize};
	margin: ${({ m }) => m};
	margin-left: ${({ ml }) => ml};
	margin-right: ${({ mr }) => mr};
	margin-top: ${({ mt }) => mt};
	margin-bottom: ${({ mb }) => mb};
`;

export const Label = styled.label<LabelProps>`
	margin-left: ${spacingMap.xs};
	vertical-align: top;
	opacity: ${({ disabled }) => disabled ? 0.33 : 1};
	cursor: ${({ disabled }) => disabled ? "not-allowed" : "pointer"};
	font-family:  ${displayFontSizes.bodyCopy.$fontFamily};
	font-size: ${displayFontSizes.bodyCopy.$fontSize};
	line-height: ${displayFontSizes.bodyCopy.lineHeight};
	font-weight: ${displayFontSizes.bodyCopy.$fontWeight};
`;

export const CheckMarkContainer = styled.span`
	position: absolute;
	left: 3px;
	cursor: pointer;
	pointer-events: none;
`;

export const CheckboxInput = styled.input<StyledCheckboxProps>`
	position: relative;
	box-sizing: border-box;
	appearance: none;
	margin: 0;

	height: 20px;
	width: 20px;

	color: ${colors.black};
	border: 1px solid ${({ isInvalid }) => isInvalid ? colors.red : colors.defaultGray};
	border-radius: 4px;

	transition: all 0.2s;
	cursor: pointer;

	&:hover {
		border-color: ${colors.black};
	}

	&:focus-within {
		outline: none;
		box-shadow: 0 0 0 4px ${colors.midBlue}40;
		border-color: ${({ isInvalid }) => isInvalid ? colors.red : colors.midBlue};
	}

	&:disabled {
		cursor: not-allowed;
		border-color: ${({ isInvalid }) => isInvalid ? colors.red : colors.defaultGray};
		opacity: 0.33;
	}

	&:checked {
		border-color: ${({ isInvalid }) => isInvalid ? colors.red : colors.midBlue};
		background: ${({ isInvalid }) => isInvalid ? colors.red : colors.midBlue};
	}

	${({ isIndeterminate, isInvalid }) => {
		if (isIndeterminate) {
			return isInvalid ? `
			border-color: ${colors.red};
			background: ${colors.red};
			` : `border-color: ${colors.midBlue};
			background: ${colors.midBlue};`;
		}
		return "";
	}}



	&:disabled:checked {
		background: ${({ isInvalid }) => isInvalid ? colors.red : colors.midBlue};
	}
`;

export const StyledDashSVG = styled.svg`
	margin-bottom: 19%;
	margin-top: 19%;
`;
